coration: none;\r\n}\r\n\r\na:hover,\r\na:visited:hover {\r\n    color: #3393f3;\r\n    text-decoration: none;\r\n}\r\n\r\n/* --- Header Styles --- */\r\n\r\n/* Overall header: 64px, across */\r\n.header {\r\n    height: 64px;\r\n\r\n    display: flex;\r\n    flex-flow: row;\r\n}\r\n\r\n/* Top Left Title - white, middle, bigger */\r\n.title {\r\n    cursor: default;\r\n    padding: 16px 8px;\r\n    vertical-align: middle;\r\n    font-size: 18pt;\r\n}\r\n\r\n/* The title is a link; fix it to look like plain text */\r\n.header .title a {\r\n    color: white;\r\n    font-weight: normal;\r\n}\r\n\r\n/* Make the search box large, fill the space, well padded, slightly grey */\r\n.header .searchBar input {\r\n    font-size: 16pt;\r\n    box-sizing: border-box;\r\n    border: none;\r\n    margin: 8px;\r\n    margin-right: 0px;\r\n    width: 100%;\r\n    padding: 8px;\r\n    color: #666666;\r\n    background: #F7F7F7;\r\n}\r\n\r\n/* Make the search icon (magnifying glass) the same as the search box and right next to it */\r\n.header .searchBar .searchIcon {\r\n    font-size: 16pt;\r\n    color: #666666;\r\n    background: #F7F7F7;\r\n    padding: 8px;\r\n    margin: 8px;\r\n    margin-left: 0px;\r\n}\r\n\r\n/* Make the buttons (top right) large, white on dark green */\r\n.buttons a {\r\n    font-size: 20px;\r\n    display: inline-block;\r\n    color: white;\r\n    height: 48px;\r\n    width: 48px;\r\n    margin: 8px 8px 0px 0px;\r\n}\r\n\r\n.buttons a:hover {\r\n    color: #999;\r\n    text-decoration: none;\r\n}\r\n\r\n/* Center the icons within the buttons */\r\n.buttons a i {\r\n    margin: 12px;\r\n}\r\n\r\n/* --- Query Stats Bar --- */\r\n\r\n/* Make the QueryStats grey and hide overflowing content */\r\n.queryStats {\r\n    padding: 6px;\r\n    background: #E3E3E3;\r\n    \r\n    white-space: nowrap;\r\n    text-overflow: ellipsis;\r\n    overflow: hidden;\r\n}\r\n\r\n/* Make the per-table buttons dark grey and spaced out */\r\n.queryStats .statsForTable {\r\n    borde